.intro{
    display: flex;
    height: 77vh;
    margin-top: 4rem;
    justify-content: space-between;
    justify-content: center;
    align-items: center;
  background-image:  linear-gradient(to right, rgb(119, 122, 122),rgb(29, 31, 36));

}

.i-left{
    display: flex;
    position: relative;
    flex:1;
    flex-direction: column;
    /* border: 1px solid; */
    gap:2rem;
   /* background-color: white; */
}
.i-name{
    display: flex;
    flex-direction: column;

}

.i-name>:nth-child(1){
  color: var(--black);
  font-weight: bold;
  font-size: 2rem;
    
}

.i-name>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 2.5rem;
      
  }

  .i-name>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 10px;
      /* font-family: var(--newfont); */
  }
.i-right{
    right: 50px;
    /* border: 1px solid red; */
  width: 45%;
}
.i-button{
width:7rem;
height: 2rem;
}
.blur{
  position:absolute ;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #edd0ff;
  /* filter: blur(212pz); */
  z-index: -9;
  top: 6%;
  left:76%;
  filter: blur(102px)
}

.blurbot{
  position:absolute ;
  width: 22rem;
  height: 14rem;
  border-radius: 50%;
  background: #8dfce6;
  /* filter: blur(212pz); */
  z-index: -9;
  left:76%;
  bottom: 20px;
  filter: blur(102px)
}