    .n-wrapper{
        height:40px;
        display:flex;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
        width:110%;
        /* margin-right: 100px; */
        margin-left: -30px;
        padding: 15px;
       /* position: fixed; */
    }
    
}

